* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}
 .flatpickr-input {
  margin-bottom: 10px !important;
  font-size: 15px !important;
  font-weight: 700 !important;
  /* text-shadow: 2px 2px 20px #fff, 5px 5px 5px #000000 !important; */
  font-family: serif !important;
}
.timer-start{
  text-transform: uppercase;
  font-size: 15px;
  font-weight: 700;
  font-family: serif;
}
.timer {
  display: flex;
  margin: -10px;
}
.field {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px;
}
.value {
  font-size: 20px;
  font-weight: 600;
  text-shadow: 2px 2px 20px #fff, 5px 5px 5px #000000;
}
.label {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 500;
  text-shadow: 2px 2px 20px #fff, 5px 5px 5px #000000;
  font-family: serif;
}
